.checklist-container {
  background-color: #FFFFFF;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  width: 350px;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.checklist-items {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.checklist-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.checklist-label {
  font-size: 16px;
  color: #333333;
  cursor: pointer;
}

/* Hide the default checkbox */
.checklist-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create custom checkbox */
.custom-checkbox {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: #FFFFFF;
  border: 1px solid #CCCCCC;
  border-radius: 4px;
  cursor: pointer;
}

/* Style for checked state */
.checklist-checkbox:checked + .custom-checkbox {
  background-color: #4F46E5;
  border-color: #4F46E5;
}

/* Create checkmark */
.checklist-checkbox:checked + .custom-checkbox::after {
  content: "";
  position: absolute;
  left: 7px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

/* Focus styles for accessibility */
.checklist-checkbox:focus + .custom-checkbox {
  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.3);
}

.checklist-divider {
  height: 1px;
  background-color: #EEEEEE;
  margin: 20px 0;
  width: 100%;
}

.checklist-button {
  background-color: #FFCC00;
  color: #333333;
  border: none;
  border-radius: 6px;
  padding: 14px 0;
  font-size: 16px;
  font-weight: 500;
  width: 100%;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.checklist-button:hover {
  background-color: #E6B800;
}

.checklist-button:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(255, 204, 0, 0.4);
}

/* Ensure the component is centered in the App for demo purposes */
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #F5F5F5;
}
